* {
    box-sizing: border-box;
  }
  
  body {
    font-family: sans-serif;
    color:$base-txt-color;
    font-size:$base-font-size;
  }
  
  .generic-card{
    background:#fff;
    padding:1em;
    margin-bottom:.5em;
    border-radius:5px;
    box-shadow:0px 5px 20px rgba(0,0,0,0.1);
  }

  h1,h2,h3,h4,h5,h6{
    color:$heading-txt-color;

  }

  
  a{
    color: $red;
  }

  a:hover{
    color:$red-darker;
  }

  .post-entry{
    @extend .generic-card;
    h2{
        margin-top:0;
    }
  }

